@import url('https://fonts.googleapis.com/css2?family=Cinzel+Decorative&family=Quicksand:wght@300&display=swap');

.sidebar {
    background-color: #00000000;
    padding: 10px;
    position: fixed;
    left: 0;
    bottom: 0;
    top: 0;
    display: flex;
    justify-content:center;
    align-items:center;
    z-index: 10;
  }
  
  .sidebar h2 {
    font-size: 1.5rem;
    margin-bottom: 10px;
  }
  
  .sidebar-menu {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .sidebar-menu-item {
    margin-bottom: 5px;
  }
  
  .sidebar-menu-item button {
    color: #ffffff;
    display: block;
    font-size: 1.2rem;
    text-decoration: none;
    background-color: transparent;
    border: none;
    font-family: 'Cinzel Decorative', cursive;

  }
  
  .sidebar-menu-item button:hover {
    color: #755540;
  }
  
  